<template>
	<div id="paybill-detail">
		<public-header title="订单详情"></public-header>
		<div class="content font-14">
			<div class="pay" v-if="order.is_pay == 1">
				<div class="box">
					<div class="icon icon-check font-24"></div>
				</div>
				<div >支付成功</div>
				<div class="desc">感谢惠顾, 请将支付结果出示给店员</div>
			</div>
			<div class="block">
				<div class="block-title van-hairline--bottom">订单信息</div>
				<van-row class="margin-15-t">
					<van-col span="8" class="title">门店名称</van-col>
					<van-col span="16" class="text-right">
						<div class="w-100 ellipsis">{{order.store.title}}</div>
					</van-col>
				</van-row>
				<van-row class="margin-15-t">
					<van-col span="8" class="title">支付金额</van-col>
					<van-col span="16" class="text-right">
						<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.final_fee}}</span>
					</van-col>
				</van-row>
				<van-row class="margin-15-t">
					<van-col span="8" class="title">支付方式</van-col>
					<van-col span="16" class="text-right">
						{{order.pay_type_cn}}
					</van-col>
				</van-row>
				<van-row class="margin-15-t">
					<van-col span="8" class="title">支付时间</van-col>
					<van-col span="16" class="text-right">
						{{order.addtime_cn}}
					</van-col>
				</van-row>
			</div>
			<div class="block">
				<div class="block-title van-hairline--bottom">费用明细</div>
				<van-row class="margin-15-t">
					<van-col span="8" class="title">消费金额</van-col>
					<van-col span="16" class="text-right">
						<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.cost}}</span>
					</van-col>
				</van-row>
				<van-row class="margin-15-t" v-if="order.no_discount_part > 0">
					<van-col span="8" class="title">不参与优惠金额</van-col>
					<van-col span="16" class="text-right">
						<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.no_discount_part}}</span>
					</van-col>
				</van-row>
				<template v-if="order.data.extra_fee_note && order.data.extra_fee_note.length > 0">
					<van-row class="margin-15-t" v-for="(item, index) in order.data.extra_fee_note" :key="index">
						<van-col span="16" class="title">{{item.name}}</van-col>
						<van-col span="8" class="text-right">
							<span class="font-12">{{Lang.dollarSign}}</span><span>{{item.fee}}</span>
						</van-col>
					</van-row>
				</template>
				<template v-if="order.data.store_extra_fee_note && order.data.store_extra_fee_note.length > 0">
					<template v-for="(item, index) in order.data.store_extra_fee_note">
						<van-row class="margin-15-t">
							<van-col span="16" class="title">{{item.name}}</van-col>
							<van-col span="8" class="text-right">
								<span class="font-12">{{Lang.dollarSign}}</span><span>{{item.fee}}</span>
							</van-col>
						</van-row>
					</template>
				</template>
				<van-row class="margin-15-t" v-if="order.discount_fee > 0">
					<van-col span="8" class="title">优惠金额</van-col>
					<van-col span="16" class="text-right c-danger">
						-<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.discount_fee}}</span>
					</van-col>
				</van-row>
				<van-divider :dashed="true" />
				<div class="text-right">
					<span class="title">实际支付金额</span>
					<span class="font-12 c-danger">{{Lang.dollarSign}}</span><span class="c-danger font-bold">{{order.final_fee}}</span>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				order: {
					store: {},
					data: {}
				},
				showPreLoading: true,
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/order/paybill/detail',
					data: {
						id: that.id
					},
					success: (res) => {
						that.order = that.util.extend(that.order, res.order);
					}
				})
			},
		},
		mounted () {
			var query = this.util.parseQuery(this.$route.query);
			this.id = query.id;
			this.onLoad()
		},
	}
</script>

<style>
	#paybill-detail .pay{
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		padding: 20px 0 40px;
		background-color: #ff2d4b;
		color: #fff;
	}
	#paybill-detail .pay .box{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		margin-bottom: 15px;
		border: 1px solid #fff;
	}
	#paybill-detail .pay .desc{
		margin-top: 10px;
		font-size: 12px;
		color: #eee;
	}
	#paybill-detail .block{
		margin: 10px;
		padding: 15px;
		background-color: #fff;
		border-radius: 5px;
	}
	#paybill-detail .block-title{
		padding-bottom: 15px;
		font-weight: 500;
	}
	#paybill-detail .title{
		color: #7a7a7a;
	}
</style>


